.sale {
  letter-spacing: -.02em;
  text-align: left;
  color: #141414;
  max-width: 100%;
  box-sizing: border-box;
  margin-left: 115px;
  margin-right: 115px;
  font-family: Epilogue;
  display: flex;
}

.sale__section {
  max-width: 1400px;
  flex-direction: column;
  display: flex;
}

.sale__title {
  margin-top: 0;
  margin-left: 0;
  padding-bottom: 80px;
  font-size: 40px;
  font-weight: 600;
  line-height: 44px;
}

.sale__part {
  flex-direction: column;
  gap: 40px;
  padding-left: 97px;
  padding-right: 98px;
  display: flex;
}

.sale__image {
  max-width: 100%;
  height: auto;
  display: block;
}

.sale__images-part {
  flex-direction: row;
  gap: 20px;
  display: flex;
}

.sale__images-list-1 {
  flex-direction: column;
  gap: 10px;
  margin: 0;
  padding-left: 0;
  display: flex;
}

.sale__images-list li {
  list-style: none;
  display: flex;
}

.sale__info {
  flex: 1;
}

.sale__subtitle {
  margin-top: 0;
  padding-bottom: 8px;
  font-size: 38px;
  font-weight: 400;
  line-height: 40px;
}

.sale__product-tag {
  color: rgba(20, 20, 20, .5);
  margin: 0;
  padding-bottom: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.sale__text {
  letter-spacing: -.01em;
  color: rgba(20, 20, 20, .5);
  margin: 0;
  padding-bottom: 40px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.sale__price {
  margin: 0;
  padding-bottom: 40px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.sale__price span {
  color: rgba(20, 20, 20, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  text-decoration: line-through;
}

.sale__description {
  margin: 0;
  padding-top: 8px;
  padding-bottom: 17px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  position: relative;
}

.sale__description:after {
  content: "";
  width: 16px;
  height: 1px;
  vertical-align: middle;
  background-color: #141414;
  margin-right: auto;
  display: inline-block;
  position: absolute;
  top: 20px;
  right: 0;
}

.sale__description:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #141414;
  display: inline-block;
  position: absolute;
  bottom: 50px;
}

@media screen and (min-width: 375px) and (max-width: 768px) {
  .sale__section {
    max-width: 100%;
    flex-direction: column;
    display: flex;
  }

  .sale {
    margin-left: 20px;
    margin-right: 20px;
  }

  .sale__part {
    padding-left: 20px;
    padding-right: 20px;
  }

  .sale__image-list-2 {
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .sale__section {
    max-width: 100%;
    display: flex;
  }

  .sale {
    margin-left: 32px;
    margin-right: 32px;
  }

  .sale__part {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .sale__section {
    max-width: 100%;
    display: flex;
  }

  .sale__part {
    flex-direction: row;
    display: flex;
  }
}

